import Vue from 'vue'
import HagoMultLang from '@yy/hago-mult-lang'
import { supportlang } from './config'
import { constant } from '@yy/hago-act-sdk'

console.log('IDC_INFO', constant)

const useLang = getUseLang()
console.log('useLanguseLanguseLang', useLang)
Vue.use(HagoMultLang)
const multLang = new HagoMultLang()
async function defaultLangPkg (titleName = false) {
  // 设置多语言
  let langPkg = {}
  try {
    langPkg = await import(`@/lang/lang_${useLang}.js`)
  } catch (e) {
    console.log('找不到对应的语言')
    langPkg = await import(`@/lang/lang_en.js`)
  }
  const lp = langPkg.default[useLang].message
  multLang.setMessage(lp)
  titleName && (document.title = lp[titleName] || '666')
}
function getUseLang () {
  let curLang = constant.CUR_LANG
  if (curLang === 'in') {
    curLang = 'id'
  }
  if (supportlang.length && supportlang.indexOf(curLang) === -1) {
    console.warn(`解析到非${supportlang}语言码，当前语言码为${curLang}，暂且用en`)
    curLang = 'en'
  }
  return curLang
}

if (useLang === 'ar') {
  document.getElementsByTagName('html')[0].setAttribute('dir', 'rtl')
}

Vue.prototype.$lang = useLang
Vue.prototype.$urlNo = constant.IDC_INFO.urlNo

export { defaultLangPkg, multLang, useLang }
