import Vue from 'vue'
import KxdReport, { KxdReportPreset } from '@yy/kxd-report'
import { constant } from '@yy/hago-act-sdk'

KxdReportPreset({
  id: 20033601,
  type: 'hago',
  uid: constant.CUR_UID
})

export const report = async data => {
  if (typeof data === 'string') {
    data = {
      function_id: data
    }
  }
  if (!window.hiidoEvent) {
    await import('@/lib/websdk')
  }
  KxdReport({ data })
}

Vue.prototype.$report = report
