import Vue from 'vue'
import HagoAppCall from '@yy/hago-app-call'
import methodConfig from '@yy/hago-app-call/lib/config/method-config'
import HacPluginSaveShare from '@yy/hago-app-call/dist/plugin/hac-plugin-save-share.esm'
import HacPluginSocket from '@yy/hago-app-call/dist/plugin/hac-plugin-socket.esm'

HagoAppCall.use(HacPluginSaveShare)
HagoAppCall.use(HacPluginSocket)

export const hagoAppCall = new HagoAppCall({
  // debug: __PROD__
  appMethods: {
    base: {
      getAppInstalled: methodConfig({ supportVersion: '2.8.0' })
    }
  }
})

Vue.prototype.$saveLog = (data) => {
  hagoAppCall.base.logInfo(data).catch(e => console.log('saveLog ', data))
}
hagoAppCall.$initSaveShare()
