<template>
  <li class="index_cont-award--item frcc">
    <img class="index_cont-award--img" :src="icon" alt="">
    <div class="index_cont-award--cont">
      <div class="index_cont-award--title fw6">{{ name }}</div>
      <p class="index_cont-award--tip">{{ desc }}</p>
    </div>
    <div class="index_cont-award--btn fw6" :class="btnStatus" @click="onClickBtn">{{ btnTxt }}</div>
  </li>
</template>

<script>
export default {
  props: {
    icon: String,
    name: String,
    desc: String,
    btnTxt: String,
    btnStatus: String,
    onClick: Function
  },
  methods: {
    onClickBtn () {
      this.onClick && this.onClick()
    }
  }
}
</script>
