<template>
  <div class="index fccc">
    <TitleBar :diamond="daimond" />
    <div class="index_cont">
      <p class="index_cont-title1 fw6">{{ $t('share_title') }}</p>
      <p class="index_cont-title2" v-html="$t('share_des', [1, `<i></i>`])"></p>
      <ul class="index_cont-award frcc">
        <Item v-for="(v, i) in shareTask" :key="i"
          :icon="require('@/img/ws.png')"
          :btnTxt="$t('share_but')"
          :name="$t('share_link')"
          :desc="$t('share_note')"
          :onClick="onShareClick.bind({},v,i)"/>
      </ul>
    </div>
    <div class="index_cont mt20">
      <p class="index_cont-title1 fw6">{{ $t('app_title') }}</p>
      <p class="index_cont-title2" v-html="$t('app_des', [1, `<i></i>`])"></p>
      <ul v-if="downloadTask.length" class="index_cont-award fccc">
        <Item v-for="(v, i) in downloadTask" :key="i"
          :icon="v.icon"
          :name="v.appName"
          :desc="v.desc"
          :btnTxt="$t('app_but')"
          :btnStatus="v.finish?'index_cont-award--btndone':''"
          :onClick="onDownClick.bind({},v,i)"/>
      </ul>
      <p class="index_cont-no" v-else>{{ $t('app_foobar') }}</p>
    </div>
    <Rule />
    <PrizeModal :down="downloadDiamond" :share="shareDiamond" v-if="showPrize" @close="onCloseModal"/>
  </div>
</template>

<script>
import PrizeModal from '@/component/PrizeModal.vue'
import { GetDiamond, CheckTask, InitTask, genShortUrl, DoTask, SetDiamondNotify } from '@/common/api'
import { hagoAppCall } from '@/common/hagoAppCall'
import TitleBar from '@/component/Title'
import Rule from '@/component/Rule'
import Item from '@/component/Item'
import { constant } from '@yy/hago-act-sdk'

const dl = (id) => `https://go.onelink.me/XHmg?pid=H5%20Pages&c=Earn%20Diamonds%20H5&af_dp=hago%3A%2F%2Fweb%2Furl%2Findex%3Finvite_uid%3D${constant.CUR_UID}%26taskID%3D${id}&af_web_dp=https%3A%2F%2Fihago.net%2F`
const dlReportMap = {
  'com.edit.clip.status.video': 0,
  'com.UCMobile.intl': 1
}

export default {
  name: 'app',
  data () {
    return {
      showPrize: false,
      downloadTask: [],
      shareTask: [],
      daimond: 0,
      downloadDiamond: 0,
      shareDiamond: 0,
      nick: ''
    }
  },
  computed: {
  },
  components: {
    PrizeModal,
    TitleBar,
    Rule,
    Item
  },
  methods: {
    async onCloseModal () {
      this.showPrize = false
      const json = await GetDiamond()
      this.daimond = json.amount
    },
    async onShareClick (v) {
      this.$report({
        function_id: 'share_wa_click',
        share_type: 2
      })
      let webUrl = dl(v.taskID)
      console.log('webUrl', webUrl)
      const json = await genShortUrl(webUrl)
      if (json.code === 1) { webUrl = json.data }
      const config = {
        webUrl,
        type: 1,
        title: `${this.nick} ${this.$t(`share_ws_title`)}`,
        content: this.$t(`share_ws_des`),
        image: require('@/data/exchang-app.png')
      }
      await hagoAppCall.saveShare['whatsapp'](config)
    },
    async onDownClick (v, i) {
      this.$report({
        function_id: 'download_click',
        download_num: dlReportMap[v.packageName] || 999
      })
      if (v.finish) { return }
      // let uri = `http://share.vclip-app.com/dynamic/vclip/cooperate/index/index.html?from=hago&utm_source=hago&utm_campaign=hagotask`
      // hagoAppCall.base.jumpUri({
      //   uri: (`hago://web/url/${encodeURIComponent(uri)}?usePageTitle=true&isFullScreen=false&disablePullRefresh=true&isShowStatusBar=true&isDeepLink=false`)
      // })
      this.$storage.set(v.taskID, true)
      v.downloadLink && (location.href = v.downloadLink)
    },
    async checkAppInstall (packetNameList) {
      const data = await hagoAppCall.base.getAppInstalled({
        packetNameList
      }).catch(e => [])
      return data
      // console.log('是否已经安装了 app', data)
    },
    async compareAppList (taskList) {
      const finishTask = {}
      const appList = []
      const rs = []
      taskList.forEach(v => {
        finishTask[v.packageName] = v
        appList.push(v.packageName)
      })
      console.log('finishTask', finishTask)
      console.log('taskList', taskList)
      console.log('appList', appList)
      const hasInstall = await this.checkAppInstall(appList)
      this.$saveLog(`任务列表 ${JSON.stringify(appList)} 已安装的app列表 ${JSON.stringify(hasInstall)} 任务列表 ${JSON.stringify(finishTask)}`)
      console.log('hasInstall', hasInstall)
      for (let i in hasInstall) {
        // 任务中是未完成的 但是已经安装的 就是可以获奖的
        if (!finishTask[i].finish && hasInstall[i]) {
          rs.push(finishTask[i])
        }
      }
      this.$saveLog(`可以获奖的 ${JSON.stringify(rs)}`)
      return rs
    },
    async init () {
    // 处理砖石数 拉取任务列表
      let index = 0
      const [diamondData, taskData] = await Promise.all([GetDiamond(), CheckTask()])
      console.log('dddddta', diamondData, taskData)
      this.daimond = diamondData.amount
      this.shareDiamond = taskData.incrDiamonds
      this.nick = taskData.nick
      taskData.finishTasks.forEach(v => {
        if (v && v.taskType === 0) { this.shareTask.push(v) }
        if (v && v.taskType === 1) {
          if (!v.finish) {
            this.downloadTask.push({ ...v, index })
            index++
          }
        }
      })
      const taskInfo = await this.compareAppList(this.downloadTask)
      console.log('taskInfo', taskInfo)
      taskInfo.forEach(v => {
        this.downloadTask[v.index].finish = true
      })
      if (taskData.newUser) {
        await InitTask({ taskIds: taskInfo.length ? taskInfo.map(v => v.taskID) : [] })
      } else {
        const doTaskList = []
        taskInfo.forEach(v => {
          if (this.$storage.get(v.taskID)) {
            doTaskList.push(DoTask({ taskID: v.taskID }))
          }
        })
        const taskAward = await Promise.all(doTaskList)
        taskAward.forEach(v => {
          this.downloadDiamond += v.reward
        })
      }
      // test
      // this.downloadDiamond = 1
      // this.shareDiamond = 9
      // testend
      if (this.downloadDiamond || this.shareDiamond) {
        this.showPrize = true
      }
      await SetDiamondNotify()
    }
  },
  async created () {
    this.$saveLog(`页面开始挂载 ${window.location.href}`)
    this.$report('show')
    await this.init()
    if (this.shareTask.length) {
    // 如果分享有任务
      this.$report({
        function_id: 'share_show',
        share_type: 2
      })
    }
    const dl = this.downloadTask.length
    if (dl) {
      // 如果下载apk
      this.$report({
        function_id: 'download_show',
        download_num: dl
      })
    }
    if (this.downloadDiamond || this.shareDiamond) {
      // 如果有可以领奖
      this.$report({
        function_id: 'diamond_get_show',
        share_income: this.shareDiamond,
        download_income: this.downloadDiamond
      })
    }
  }
}
</script>

<style lang="scss">
  @import '~@/css/lib/_mixin';
  .mt20 {
    margin-top: 20px;
  }
  .fw6 {
    font-weight: 600;
  }
  .index {
    width: 100%;
    height: 100%;
    color: #222;
    font-size: 28px;
    justify-content: flex-start !important;
    overflow-x: hidden;

    &_cont {
      width: 660px;
      background-color: #ffffff;
      box-shadow: 0px 10px 30px 0px rgba(209, 235, 254, 0.7);
      border-radius: 6px;
      text-align: left;
      padding: 40px 27px 25px;
      box-sizing: border-box;
      &-title1 {
        font-size: 44px;
      }
      &-title2 {
        font-size: 22px;
        color: #999;
        margin-top: 20px;
        margin-bottom: 20px;
        i {
          @include bg('diam-s', 24, 19);
          display: inline-block;
        }
      }
      &-no {
        color: #999;
        text-align: center;
        font-size: 22px;
        margin: 45px auto;
      }
      &-award {
        &--item {
          width: 100%;
          justify-content: space-between !important;
          margin-bottom: 20px;
        }
        &--img {
          width: 100px;
          height: 100px;
        }
        &--cont {
          flex: 1;
          text-align: left;
          padding: 0 20px;
        }
        &--title {
          font-size: 26px;
        }
        &--tip {
          font-size: 22px;
          color: #999;
          margin-top: 8px;
          overflow : hidden;
          text-overflow: ellipsis;
          display:  -webkit-inline-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        &--btn {
          min-width: 120px;
          height: 64px;
          text-align: center;
          line-height: 64px;
          background-color: #ff4a6d;
          border-radius: 6px;
          color: #fff;
          padding: 0 20px;
          box-sizing: border-box;
        }
        &--btndone {
          background-color: #bbb !important;
        }
      }
    }

  }
</style>
