import Axios from 'axios'
import { Request, constant } from '@yy/hago-act-sdk'
import bus from './bus'
import { hagoAppCall } from './hagoAppCall'
import { useLang } from './loadMultLang'
// import jsonp from '@yy/jsonp'

const baseUrl = constant.ENV !== 'prod' ? '//i-test-863.ihago.net' : `//i-${constant.IDC_INFO.urlNo}.ihago.net`
console.log('constant', constant)

const request = new Request({
  handler: Axios,
  preHandler: () => {
    bus.$showLoading()
  },
  postHandler: () => {
    bus.$hideLoading()
  }
})
// jsonp.mixin({
//   beforeSend () {
//     bus.$showLoading()
//   },
//   always () {
//     bus.$hideLoading()
//   }
// })

const errCatch = (e) => {
  console.log('ooop error', e)
  bus.$hideLoading()
  bus.$showToast(bus.$t('loadingFailed'))
}

const reqTry = 1
const serviceName = 'net.ihago.diamond.srv.share' // 微服务名
let hdid = ''
// const activityId = 100005

export const GetDiamond = () => {
  const methodName = 'DiamondShare.GetDiamond' // 微服务方法名
  return request.post(serviceName, methodName, {}, reqTry)
    .catch(errCatch)
}

export const DoTask = (reqData) => {
  const methodName = 'DiamondShare.DoTask' // 微服务方法名
  return request.post(serviceName, methodName, { hdid, ...reqData }, reqTry)
    .catch(errCatch)
}

export const CheckTask = async () => {
  const msg = await hagoAppCall.base.getHdid().catch(e => 123456)
  console.log('msg', msg)
  hdid = msg.hdid || 'test125123152121'
  const methodName = 'DiamondShare.CheckTask' // 微服务方法名
  return request.post(serviceName, methodName, { hdid, lang: useLang }, reqTry)
    .catch(errCatch)
}

export const InitTask = async (reqData) => {
  const methodName = 'DiamondShare.InitTask' // 微服务方法名
  return request.post(serviceName, methodName, { hdid, ...reqData }, reqTry)
    .catch(errCatch)
}

export const SetDiamondNotify = async (reqData) => {
  const methodName = 'DiamondShare.SetDiamondNotify' // 微服务方法名
  return request.post(serviceName, methodName, { hdid, ...reqData }, reqTry)
    .catch(errCatch)
}

export const genShortUrl = (url) => {
  const data = {
    url,
    uq: 1,
    token: new Date().getTime()
  }
  return Axios(`${baseUrl}/d/_gen`, {
    withCredentials: true,
    type: 'post',
    params: data
  }).then(data => data.data)
}
