<template>
  <div class="app">
    <VuePageLoading :show='showLoading' />
    <VueToast :show='showToast' :content='toastContent' position='middle' transitionName='' />
    <Index />
  </div>
</template>

<script>
import { VueToast } from '@yy/vue-toast'
import { VuePageLoading } from '@yy/vue-page-loading'
import npc from '@yy/npc-hago'
import Index from './Index.vue'
import Vue from 'vue'

export default {
  name: 'app',
  data () {
    return {
      showLoading: false,
      showToast: false,
      toastContent: ''
    }
  },
  components: {
    Index,
    VueToast,
    VuePageLoading
  },
  methods: {},
  async mounted () {
    npc.reportFMP({
      moreInfo: {
        js: window.VUE_START - window.JS_START,
        render: Date.now() - window.VUE_START
      }
    })
  },
  created () {
    Vue.prototype.$showLoading = () => {
      this.showLoading = true
    }
    Vue.prototype.$hideLoading = () => {
      this.showLoading = false
    }
    Vue.prototype.$showToast = (cont) => {
      this.showToast = true
      this.toastContent = cont
      setTimeout(() => { this.showToast = false }, 2000)
    }
  }
}
</script>
<style lang="scss">
  .app {
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
  }
</style>
