import '@/common/JsStart'
import Vue from 'vue'
import App from '@/module/App'
import { checkVersion } from '@yy/zip-version-checker'
import Sentry from 'Sentry'
import { asyncLoadJs } from '@yy/load'
import { constant } from '@yy/hago-act-sdk'
import { os } from '@yy/platform'
import { defaultLangPkg } from '@/common/loadMultLang'
import '@/common/report'
import '@/common/util'

import '@/css/index.scss'
async function init () {
  window.VUE_START = Date.now()

  if ((constant.ENV === 'test' && __PROD__) || /debug/g.test(location.search)) {
    await asyncLoadJs('https://static.ihago.net/a/ihago-libs/assets/data/vconsole/3.2.0/vconsole.min.js')
    new window.VConsole() // eslint-disable-line
  }

  new Vue({ // eslint-disable-line
    el: '#app',
    render: h => h(App)
  })

  defaultLangPkg('page_title')
  Vue.prototype.$isIos = os.iOS

  if (__PROD__) {
    Sentry.init({
      dsn: SENTRY_DNS,
      release: SENTRY_RELEASE,
      environment: SENTRY_ENV,
      integrations: [new Sentry.Integrations.Vue({ Vue })]
    })
  }

  checkVersion()
}
init()
